;\n  --lighter-red: #bf5555;\n\n  --lighter-green: #4fd53d;\n\n  --light-shadow: rgba(0, 0, 0, 0.4);\n  --lighter-shadow: rgba(0, 0, 0, 0.2);\n\n  --upload-file-button: #4fd53d;\n}\n\nbody {\n  font-size: var(--font-regular);\n  font-family: var(--font-family);\n}\n\n.container {\n  display: grid;\n  /* grid-template-columns: 1; */\n  grid-template-rows: 50px auto 20px;\n}\n\n.header {\n  display: grid;\n  background-color: var(--light-purple);\n  color: var(--lighter-grey);\n\n  grid-template-columns: 5fr 1fr 1fr;\n  align-items: center;\n  padding: 1em;\n}\n\n/* title of the application related */\n.title {\n\tfont-weight: var(--font-weight-regular);\n\ttext-transform: uppercase;\n}\n\n/* user logo related */\n.user-info {\n\t/*grid-column: 6;*/\n\tposition: relative;\n\tjustify-self: end;\n}\n\n.user-greeting {\n\tdisplay: block;\n }\n \n .ReactModal__Overlay {\n   background-color: rgba(0, 0, 0, 0.75) !important;\n }\n \n .ReactModal__Content {\n   position: absolute;\n   top: 50%;\n   left: 50%;\n   right: auto;\n   bottom: auto;\n   margin-right: -50%;\n   transform: translate(-50%, -50%);\n   background: #fff;\n   padding: 20px;\n   border-radius: 4px;\n   outline: none;\n }\n \n .modal {\n   width: 500px;\n   max-width: 90%;\n   background: white;\n   padding: 20px;\n   border-radius: 8px;\n   box-shadow: 0 5px 15px rgba(0,0,0,0.3);\n }\n \n .overlay {\n   position: fixed;\n   top: 0;\n   left: 0;\n   right: 0;\n   bottom: 0;\n   background-color: rgba(0, 0, 0, 0.75);\n   display: flex;\n   align-items: center;\n   justify-content: center;\n }\n \n .modal h2 {\n   margin-top: 0;\n   margin-bottom: 20px;\n }\n \n .modal form {\n   display: flex;\n   flex-direction: column;\n }\n \n .modal form > div {\n   margin-bottom: 15px;\n }\n \n .modal label {\n   display: block;\n   margin-bottom: 5px;\n }\n \n .modal input[type=\"text\"] {\n   width: 100%;\n   padding: 8px;\n   box-sizing: border-box;\n }\n \n .modal select {\n   width: 100%;\n   padding: 8px;\n   box-sizing: border-box;\n }\n \n .modal button {\n   padding: 10px;\n   margin-top: 10px;\n }\n\n  .create-room-container {\n\tjustify-self: center; /* Center the CreateChatRoom component */\n\tmargin-left: 20px; /* Add some spacing */\n  }\n\n\n/* Footer realted */\n.footer {\n  background-color: var(--light-purple);\n  z-index: 1;\n}\n\n.left-arrow {\n  color: var(--lighter-grey);\n  padding-left: 5px;\n  cursor: pointer;\n  font-size: 1.5em;\n}\n  \n\n\n\n.bold-title {\n  font-weight: var(--font-weight-bold);\n}\n\n/* Login related */\n.login {\n  height: calc(100vh - 70px);\n  display: grid;\n  place-items: center;\n}\n\n.login-title {\n  margin-bottom: 20px;\n  color: var(--light-purple);\n}\n\n.login-form {\n  border: solid 1px var(--dark-grey);\n  box-shadow: 0 0 10px var(--light-shadow);\n  justify-self: center;\n  padding: 4em;\n}\n\ninput[type=\"password\"],\ninput[type=\"text\"] {\n  width: 100%;\n  border: 0;\n  outline: 0;\n  border-bottom: solid 1px var(--light-purple);\n  padding: 0.9em;\n  font-size: 0.7em;\n  letter-spacing: 1px;\n}\n\ninput[type=\"password\"]:hover,\ninput[type=\"text\"]:hover,\ninput[type=\"password\"]:focus,\ninput[type=\"text\"]:focus {\n  border-bottom: solid 2px var(--light-purple);\n}\n\n/* Error message in the Login screen */\n.error-message {\n  font-size: 0.7em;\n  background-color: var(--light-shadow);\n  padding: 1em;\n  color: var(--lighter-red);\n  max-height: 33px;\n  border-left: solid 2px;\n  border-radius: 2px;\n}\n\n/* Success message in the Login screen */\n.success-message {\n\tfont-size: 0.7em;\n\tbackground-color: var(--light-shadow);\n\tpadding: 1em;\n\tcolor: var(--lighter-green);\n\tmax-height: 33px;\n\tborder-left: solid 2px;\n\tborder-radius: 2px;\n  }\n\n.user-icon {\n  border-radius: 50px;\n  width: 30px;\n  height: 30px;\n\n  position: absolute;\n  top: -6px;\n  left: -35px;\n  border: solid 1px var(--dark-grey);\n  text-transform: uppercase;\n  padding: 0.2em;\n  font-size: 1em;\n}\n\n.user-greeting {\n  display: block;\n}\n\n.user-greeting:hover {\n  cursor: pointer;\n}\n\n/* drop down menu */\n.user-info > .drop-down {\n  display: none;\n}\n\n.user-info:hover > .drop-down {\n  display: block;\n}\n\n.drop-down {\n  padding: 1em;\n  position: absolute;\n}\n\n/* Log out div */\n.logout {\n  font-size: 0.7em;\n  padding: 1em;\n  position: absolute;\n  right: 0;\n  top: 23px;\n  color: var(--light-purple);\n  background-color: var(--lightest-grey);\n  z-index: 1;\n  border-radius: 2px;\n}\n\n.logout::after {\n  content: \"\";\n  border-right: solid 5px transparent;\n  border-bottom: solid 5px var(--lightest-grey);\n  border-left: solid 5px transparent;\n  position: absolute;\n  top: -5px;\n  right: 8px;\n  width: 0;\n  height: 0;\n}\n\n.logout-link {\n  text-decoration: none;\n  color: var(--light-purple);\n  padding-left: 0.5em;\n  text-align: left;\n  padding-right: 0.5em;\n  background-color: var(--lightest-grey);\n}\n\n.logout-link:hover,\n.logout-link:focus {\n  opacity: 0.6;\n}\n\n/* Content related */\n.content {\n\tdisplay: grid;\n\tgrid-template-columns: 1fr 2fr;\n\theight: calc(100vh - 70px);\n  }\n\n  .left-column {\n\tdisplay: grid;\n\tgrid-template-rows: repeat(3, 1fr); /* Three rows with equal height */\n\theight: calc(100vh - 70px);\n  }\n\n/* Rooms panel related styles */\n.rooms-panel {\n  overflow-x: hidden;\n  overflow-y: auto;\n  height: calc(100vh - 70px);\n}\n\n.rooms-panel-superuser {\n  overflow-x: hidden;\n  overflow-y: auto;\n  height: calc(50vh);\n}\n\n.room-info {\n  position: relative;\n  display: grid;\n  grid-template-columns: 1fr 5fr 1fr;\n  margin-bottom: 10px;\n}\n\n.trending-info {\n\tposition: relative;\n\tdisplay: grid;\n\tgrid-template-columns: 1fr 5fr 1fr;\n\tmargin-bottom: 10px;\n\theight: calc(10vh);\n}\n\n.room-initials {\n  border-radius: 50px;\n  border: solid 1px var(--dark-grey);\n  align-self: center;\n  justify-self: center;\n  padding: 0.5em;\n  text-transform: uppercase;\n  position: relative;\n}\n\n.trending-initials 